<template>
	<footer class="nav-tab">
		<router-link to="/home">
			<span class="iconfont icon-home"></span>
			首页
		</router-link>
		<router-link to="/bourn">
			<span class="iconfont icon-dingwei"></span>
			目的地		
		</router-link>
		<router-link to="/aide">
			<span class="iconfont icon-zhizhang9"></span>
			行程助手			
		</router-link>
		<router-link to="/message">
			<span class="iconfont icon-xiaoxi"></span>
			消息		
		</router-link>
		<router-link to="/mine">
			<span class="iconfont icon-wo"></span>
			我的						
		</router-link>
	</footer>
</template>

<script>
	import Vue from 'vue'
	const NavTab = Vue.extend({
		name:'navtab'
	})
	Vue.component('nav-tab',NavTab)
	export default NavTab
	
</script>

<style scoped lang="scss">
	.nav-tab{
		flex-grow: 0;
    	flex-shrink: 0;
    	position: fixed;
    	bottom: 0;
		width: 100%;
	    height:2.35rem;
	    display: flex;
	    flex-direction: row;
	    background-color: #fff;
	    justify-content: space-around;
	    align-items: center;
	    a{
	    	flex-grow: 1;
	    	display: flex;
	    	flex-flow: column;
	    	align-items: center;
	    	justify-content: space-around;
	    	font-size: .3rem;
	    	color: #777;
	    	.iconfont{
	    		font-size: .9rem;
	    	}
	    	&.router-link-active{
	    		color: #0089dc;
	    	}
	    }
	}
</style>


