<template>
	<div class="aide">
		<div class="order  root-box">
			<div class="aide_nav">
				<p>行程助手</p>
			</div>
			<div class="aide_box" id="wrapper">
				<div id="scroller">
					<div class="img-trip">
						<img src="http://m.toursforfun.com/img/mytrip/icon_other_status.png">
						<div class="btn-login">
							请登录后查看
						</div>
					</div>
					<div class="line">
						<span>品质精选</span>
					</div>
					<div class="aide_box_centent" v-for="data in datas">

					
						<router-link :to="{name:'page',params:{id:data.new_product_id}}" class="product-item">
							<div class="product-img" >
								<img :src="data.image">
							</div>
							<div class="product-info">
								<h4>
									{{data.product_name}}
								</h4>

								<div class="price-wrap">
									<div class="list-tag">

									</div>
									<div class="count">
										已售 <span>{{data.order_count}}</span>
									</div>
									<div class="price">
										<span>¥</span>
										<span class="value">{{data.default_price*6.7427|formatMoney}}</span>
										<span class="price-qi">起</span>
									</div>
								</div>
							</div>
						</router-link>

					</div>
				</div>
			</div>
			
			<nav-tab></nav-tab>
				
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import NavTab from './NavTab'
	import axios from  'axios'
	export default {
		name: 'aide',
		data() {
			return {
				datas:{}
			}
		},
		filters:{
			formatMoney:function (value) {
			    return value.toFixed(2)
			}
		  },	
		methods:{
			getSlides(){		
					axios.get('https://app.toursforfun.com/config/index/v1')
				.then(function(res){
					let data=res.data.data.recommend_products[0].products_info
					this.datas=data
					console.log(this.datas[0].new_product_id)
				}.bind(this))
			},
		},
		created(){
			this.getSlides()
		},
		mounted(){
	 	}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
	.aide {
		overflow: hidden;
		.aide_nav {
			position: fixed;
			width: 100%;
			top: 0;
			background: white;
			box-shadow: 0 3px 2px rgba(88, 88, 88, .1);
			z-index: 9;
			p {
				display: block;
				margin: 0 10px;
				min-width: 30px;
				height: 43px;
				text-align: center;
				white-space: nowrap;
				font-size: 18px;
				font-weight: 700;
				color: #363636;
				line-height: 44px;
			}
		}
		.aide_box {
			margin-top: 44px;
			width: 100%;
			margin-bottom: 2.2rem;
			.img-trip {
				text-align: center;
				padding: 30px 24px 0;
				overflow: hidden;
				height: auto;
				.btn-login {
					width: 146px;
					display: block;
					margin: 20px auto 58px;
					border: 1px solid #e0e0e0;
					font-size: 18px;
					font-weight: 400;
					color: #e0e0e0;
					line-height: 28px;
					border-radius: 15px;
				}
			}
		}
		.line {
			height: 1px;
			border-top: 1px solid #e0e0e0;
			text-align: center;
			margin: 0 24px;
			span {
				position: relative;
				top: -14px;
				font-size: 18px;
				background: #fff;
				padding: 0 20px;
			}
		}
		.aide_box_centent {
			padding: 0 24px;
			background-color: #fff;
			.product-item {
				display: block;
				padding: 24px 0;
				border-bottom: 1px solid #e0e0e0;
				.product-img {
					width: 100%;
					min-height: 49vw;
					margin-right: 16px;
					background-position: center;
					background-repeat: no-repeat;
					background-size: cover;
					img {
						width: 100%;
					}
				}
				.product-info{
					display: block;
  				color: #363636;
					h4{
						line-height: 20px;
				    overflow: hidden;
				    text-overflow: ellipsis;
				    display: -webkit-box;
				    -webkit-line-clamp: 2;
				    -webkit-box-orient: vertical;
				    margin: 24px 0;
				    font-size: 14px;
				    color: #4e4e4e;
				    font-weight: normal;
					}
					.price-wrap{
						display: flex;
    
				    align-items: center;
				    padding-top: 3px;
				    font: 12px Helvetica Neue Regular;
				    color: #363636;
				    .count{
				    	flex: 1;
					    margin-top: -2px;
					    height: 18px;
					    color: #b6b6b6;
					    line-height: 18px;
				    }
					}
					.price{
						font-size: 12px;
				    line-height: 8px;
				    text-align: right;
				    color: #fb5f10;
				    .value {
				    	font-size: 24px;
    					font-weight: 700;
				    }
				    .price-qi{
				    	color:#363636
				    }
					}
				}
			}
		}
	}
</style>


