<template>
	<div class="modal-backdrop active">
		<div class="nav">
			<h3>评论</h3>
		</div>

		<div class="com-wrap">
			<header class="com-summery">
				<span class="com-sTitle">游客满意度:</span>
				<span class="com-score ng-binding">91.3%</span>
				<span class="com-total">共<em class="ng-binding">{{datas.data.total}}</em>条评论</span>
			</header>
			<ul class="com-list" >
				<li class="com-item" v-for="data in datas.data.items">
					<div class="com-header">
						<div class="meta ng-binding">
							{{data.name}}
							<span percent="80" class="star">
								<div class="tff-star-wrap">
									<div class="tff-star-inner" :style="{width:data.travel_rating+'%'}"></div>
								</div>
							</span>
							<small class="com-time ng-binding">{{data.time.split(" ")[0]}}</small>
						</div>
					</div>
					<div class="com-content ng-binding">
						{{data.content}}
					</div>
				</li>
			</ul>
		</div>

		<div class="footer">
			<h3  @click="getPosition">关闭</h3>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import axios from 'axios'
	import {mapState} from 'vuex'
	var PageMore = Vue.extend({
		name: "page-more",
		data(){
			return{
				datas:{}
			}
		},
		methods:{
			getPosition(){
				this.$store.commit('getPosition')
			},
			getSlides() {
				axios.get('https://app.toursforfun.com/api/product/101470116/comment')
				.then(function(res){
					this.datas=res.data
					
				}.bind(this))
			}
		},
		mounted(){
			this.getSlides()
		}
		
	})
	
	
	
	
	export default PageMore
</script>

<style scoped lang="scss">
	.modal-backdrop {
		display: fixed;
		width: 100%;
		height: 100%;
		.nav {
			box-shadow: 0 1px 2px rgba(88, 88, 88, .1);
			position: fixed;
			top: 0;
			background: #fff;
			text-align: center;
			width: 100%;
			h3 {
				height: 44px;
				padding: 8px 0;
				font-size: 20px;
				text-align: center;
				font-weight: 600;
				width: 100%;
				line-height: 30px;
			}
		}
		.com-wrap {
			margin-top: 45px;
			.com-summery {
				padding: 0 24px;
				font-size: 18px;
				border-top: 0 none;
				line-height: 46px;
				border-color: #E0E0E6;
				border-bottom: 1px solid #CFCED3;
				.com-sTitle {
					font-weight: 700;
				}
				.com-score {
					padding-left: 5px;
					font-size: 18px;
					font-weight: 700;
					color: #0090F3;
				}
				.com-total {
					color: #126CC9;
					float: right;
					.ng-binding {
						color: #0090F3;
						font: inherit;
					}
				}
			}
			.com-list {
				.com-item {
					color: #3f3f3f;
					border-color: #E0E0E6;
					padding: 48px 0;
					margin: 0 24px;
					font-size: 14px;
					    border-bottom: 1px solid #CFCED3;
					.com-header {
						margin-bottom: 24px;
						display: flex;
						.meta {
							flex: 1;
							font-size: 14px;
							.star {
								font-size: 14px;
								width: 70px;
								.tff-star-wrap {
									display: inline-block;
									width: 70px;
									height: 17px;
									background: url(../../../static/image/star-off.png) 0 3px repeat-x;
									background-size: 14px 14px;
									padding-top: 3px;
									.tff-star-inner {
										height: 100%;
										background: url(../../../static/image/star_on.png) repeat-x;
										background-size: 14px 14px;
									}
									
								}
							}
							.com-time {
										float: right;
										color: #6c6c6c;
										font-size: 85%;
										    vertical-align: baseline;
										line-height:21px;
									}
						}
					}
					.com-content{
						line-height: 20px;
					}
				}
			}
		}
		.footer {
			position: fixed;
			width: 100%;
			bottom: 0;
			box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
			text-align: center;
			background:#fff;
			border-top: 1px solid #eaeaea;
			h3 {
				color: #363636;
				font-size: 18px;
				font-weight: 600;
				line-height: 44px;
			}
		}
	}
</style>


