<template>
  <div class="page">
  	<i class="iconfont icon-xiaoyuhao" @click="routerBack"></i>
 <transition enter-active-class="animated bounceInDown"  leave-active-class="animated bounceInUp">

  	<page-centent  v-show="!isShow" :id="id"></page-centent>
		</transition>	
		<page-tab   v-show="!isShow" :id="id"></page-tab>
		
					<transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceInDown">

			<page-more v-show="isShow"  :id="id"></page-more>
		</transition>
		
  </div>
</template>

<script>
	import Vue from 'vue'
	import {mapState} from 'vuex'
	import PageTab from './PageTab'
	import PageCentent from './PageCentent'
	import PageMore from './PageMore'
	export default {
	  name: 'page',
	  data () {
	    return {
	    }
	  },
	  computed:{
			...mapState(['isShow'])
	  },
	  methods:{
  		showPosition(){
	  		this.$store.state.isShow=!this.$store.state.isShow
	  	},
	  	routerBack() {
				this.$router.go(-1);
			}
	  },
	  mounted(){
	  	this.$store.state.id=this.$route.params.id
	  },
	  components:{
	  	PageTab,
	  	PageCentent,
	  	PageMore
	  }
	}
</script>

<style scoped lnag="scss">
		.page .icon-xiaoyuhao{
	    position: absolute;
	    z-index: 1000;
	    font-size: 25px;
	    color: #fff;
	    margin: 10px;
		}
</style>



