<template>
	<div>
		<div class="buttons-tab">
			<span :class="{'Hactive':Active==$index}" @click="getIndex(proName,$index)" v-for="(proName,$index) in proNames">{{proName.name}}</span>			
		</div>
		
		<div class="product-box">
			
			<div class="product" v-for="product in products">
				<router-link :to='{name:"page",params:{id:product.new_product_id}}'>
					<img :src="product.image.indexOf('http')!=-1?product.image:'http://dn-toursforfun.qbox.me/images/'+product.image" alt="" />
					<div class="Hinfomation">
						<div class="Hname">
							<h4>
								{{product.product_name}}
							</h4>
						</div>
						<div class="Hdata">
							<div class="num">
								<span>已售</span>
								<span>{{product.order_count}}</span>
							</div>
							<div class="price">
								<span class="price-sp1">￥</span>
								<span class="price-sp2">{{product.default_price*6.7 |formatMoney}}</span>
								<span class="price-sp3">起</span>
							</div>
						</div>	
					</div>	
				</router-link>
						
			</div>
		</div>
	</div>
</template>

<script>
	import axios from  'axios'
export default {
  name: 'home-option',
  props:['position','showPosition'],
  data () {
  	
    return {
    	Active:0,
    	proNames:[] ,
    	products:[],
    	index:null,
    	id:null
    	
    }    
  },
  filters:{
	formatMoney:function (value) {
	    return value.toFixed(2)
	}
  },	
  methods:{
  	getProduct(){
  		axios.get('https://app.toursforfun.com/config/index/v1')
  		.then(function(response){ 
  		
  			this.proNames = response.data.data.recommend_products  
  			console.log(this.proNames)
  		}.bind(this))  		
  	} ,
  	getIndex(item,index){ 
  		this.Active =index
		this.products = item.products_info
//		console.log(this.products)
  	}
  	
  },
  mounted(){
  	this.getProduct()
  	
  },
  updated(){
	
  },
  watch:{
  	proNames(){
  		this.getIndex(this.proNames[0],0)
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
	.buttons-tab{
		width:100%;	
		height:2.25rem;
		margin:1.5rem 0;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		span{
			display:inline-block ;
			height: 2.2rem;
		    font-size: 0.8rem;
		    color: #363636;
		    font-weight: 400;
		    line-height: 2.2rem;		    
		}
		.Hactive{
			color: #0090f2;
    		font-weight: 600;
    		border-bottom: 1px solid #0090f2;
		}
	}
	.product-box{
		padding: 0 1.2rem;
    	background-color: #fff;
    	margin-bottom: 2.35rem;
		.product{
			img{
				width:100%;
			}
		}
		.Hinfomation{
			padding: 0.8rem 0;
    		border-bottom: 1px solid #e0e0e0;
			.Hname{
				h4{					
					line-height:1rem;					
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					font-weight: 400;					
					margin:0.5rem 0;
					font-size:0.7rem ;
					color:#363636;
				}			
			}
			.Hdata{
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				.num{
					span{
						color:#b6b6b6;
						font-size: 0.6rem;
					}
				}
				.price{
					.price-sp1{
						font-size: 0.4rem;
						color:#fb5f10;
					}
					.price-sp2{
						font-size: 1.2rem;
						color:#fb5f10;
						font-weight: 400;
					}
					.price-sp3{
						font-size: 0.4rem;
					}
				}
			}	
		}			
	}
</style>
