<template>
  <div class="">
  		<div v-if="isChange">
  			<home-header :getChange='getChange' ></home-header> 	
				<home-content></home-content>						
				<home-option></home-option>		
	  		<nav-tab></nav-tab>
  		</div>  			 		
			<transition enter-active-class="animated zoomInUp"  leave-active-class="animated zoomOutDown">
  			  <home-search  v-if="!isChange" :getChange='getChange'></home-search>
  			  
  	 </transition>					  	
			<transition enter-active-class="animated fadeInUpBig" leave-active-class="animated fadeOutDownBig">
				<home-phone v-if="phone"></home-phone>
			</transition>
  </div>
  
</template>

<script>
	import {mapState} from 'vuex'
import NavTab from './NavTab'
import HomeHeader from './HomeComponents/HomeHeader'
import HomeContent from './HomeComponents/HomeContent'
import HomeOption from './HomeComponents/HomeOption'
import HomeSearch from './HomeComponents/HomeSearch'
import HomePhone from './HomeComponents/HomePhone'
export default {
	
  name: 'home',
  data () {
    return {
    	 isChange:true, 
    	 
    }
  },
  computed:{
		 ...mapState(['phone'])
  },
  methods:{
		getChange(){  		
  		let that = this
  		setTimeout(()=>{  		  			
  			that.isChange =!this.isChange
  		},0) 
   }
   
  },
  mounted(){
	
  },
  components:{
  	HomeHeader,
		HomeContent,
  	HomeOption,
  	HomeSearch,
  	HomePhone
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



