<template>
	<div class="list-box clearfix">
		<v-scroll :on-refresh="onRefresh" :on-infinite="onInfinite" >
			<router-link :to="{ name:'page',params:{id:i.new_product_id}}" v-for="(i,index) in listdata">		
				<div class="list-item">
					<img :src="i.image.indexOf('http')!=-1?i.image:'http://dn-toursforfun.qbox.me/images/'+i.image" alt="" />
					<p class="list-title">{{i.product_name}}</p>
					<div class="list-price">
						<div class="list-icon">
							<i></i>
							<i></i>
						</div>
						<p class="list-num">已售{{i.order_count}}</p>
						<li>
							<span>￥</span>
							<h5>{{i.default_price*6.7424 |formatMoney}}</h5>
							<span>起</span>
						</li>
					</div>
				</div>
			</router-link> 
			<router-link :to="{ name:'page',params:{id:i.new_product_id}}" v-for="(i,index) in downdata">
				<div class="list-item">
					<img :src="i.image.indexOf('http')!=-1?i.image:'http://dn-toursforfun.qbox.me/images/'+i.image" alt="" />
					<p class="list-title">{{i.product_name}}</p>
					<div class="list-price">
						<div class="list-icon">
							<i></i>
							<i></i>
						</div>
						<p class="list-num">已售{{i.order_count}}</p>
						<li>
							<span>￥</span>
							<h5>{{i.default_price*6.7424 |formatMoney}}</h5>
							<span>起</span>
						</li>
					</div>
				</div>
			</router-link> 
		</v-scroll>
	</div>
</template>

<script>
	import Scroll from '../Common/scroll'
	import axios from 'axios'
	import Vue from 'vue'
	export default {
		name: 'login-a',
		data() {
			return {
				counter: 2, //默认已经显示出15条数据 count等于一是让从16条开始加载
				listdata: [], // 下拉更新数据存放数组
				downdata:[],
			}
		},
		filters: {
			formatMoney: function(value) {
				return value.toFixed(2)
			}
		},
		methods: {
			getList() {
				let that = this;
				axios.get('https://app.toursforfun.com/search/all/product_list?page=1&page_size=10')
					.then((response) => {
						this.listdata = response.data.data.product_list;
					}, (response) => {
						console.log('error');
					});
			},
			onRefresh(done) {
				this.getList();
				done() 
			},
			onInfinite(done) {
				let that = this;
				axios.get('https://app.toursforfun.com/search/all/product_list?page=' + that.counter++ + '&page_size=10')
					.then((response) => {
						let arr=response.data.data.product_list
						for(let i in arr ){
							console.log(arr[i])
							that.downdata.push(arr[i]) 
						}
						done()
					}, (response) => {
						console.log('error');
					});
			}
		},
		mounted() {
			this.getList()
		},
		components: {
			'v-scroll': Scroll
		},
		watch: {

		}
	}
</script>
<style scoped lang='scss'>
	.list-box {
		.down {
			margin: 0 1rem;
		}
	}
	
	.list-item {
		padding: 1rem 0;
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-flow: column;
		box-shadow: 0 1px 2px rgba(88, 88, 88, .1);
		img {
			width: 100%;
		}
		.list-title {
			font-size: 0.7rem;
			padding: 1rem 0;
		}
		.list-price {
			display: flex;
			align-items: center;
			.list-icon {
				width: 10%;
				min-height: 0.9rem;
			}
			.list-num {
				color: #b6b6b6;
				text-align: left;
				flex: 1;
				font-size: 0.4rem;
			}
			li {
				color: #b6b6b6;
				text-align: center;
				display: flex;
				justify-content: center;
				align-items: center;
				width: 40%;
				font-size: 0.3rem;
				color: #fb5f10;
				span:nth-child(1) {
					margin-right: 0.25rem;
					display: inline-block;
					padding-top: 0.3rem;
				}
				h5 {
					font-weight: 700;
					font-size: 1.2rem;
				}
				span:nth-last-child(1) {
					padding-top: 0.3rem;
					color: black;
					display: inline-block;
					margin-left: 0.25rem;
				}
			}
		}
	}
</style>


