<template>
	<div class="finder root-box">
		<div class="title title-center header-item">消息</div>
		<my-loading :isShow="isShow"></my-loading>
		<div class="scroll" style="transform: translate3d(0px, 0px, 0px) scale(1);">
			<div class="news-list tff-container" v-if="" style="">
				<div class="news-item" v-for="item in messageList">

					<p class="news-content" v-text="item.message_content"></p>
					<div class="news-other">
						<span class="" v-text="item.send_time"></span>

						<router-link :to="{ name:item.message_extras.news_type=='activity'? (item.message_extras.news_id==1?'activity':'item'):'page', params: { dataId: item.message_id ,type:item.message_extras.news_type,id1:item.message_extras.news_id,id:'item.admin_user_id'}}">详情 &gt;</router-link>
					</div>
				</div>
			</div>
		</div>
		<p class="text-center txt-gray list-margin" v-if="!isShow">
			到底了哦~~
		</p>
		<nav-tab></nav-tab>
	</div>

</template>

<script>
	import MyLoading from './loading/loading'
	import NavTab from './NavTab'
	import axios from 'axios'
	import Vue from 'vue'
	Vue.use(MyLoading)
	export default {
		name: 'message',
		data() {
			return {
				messageList: [],
				isShow: true
			}
		},
		methods: {
			getMessage() {
				axios.get('https://app.toursforfun.com/api/messages?device=all&page=1&page_size=10')
					.then(function(response) {
						this.messageList = response.data.data.messageList

					}.bind(this))

			}
		},
		mounted() {
			let that = this
			setTimeout(() => {
				that.getMessage()
				that.isShow = false
				console.log(that.isShow)
			}, 2000)
		},
		components: {
			MyLoading
		}
	}
</script>

<!-- Add 'scoped" attribute to limit CSS to this component only -->
<style scoped>
	.title {
		color: #363636;
		font-size: 18px;
		font-weight: 700;
		text-align: center;
	}
	
	.scroll {
		user-select: none;
		-webkit-transform-origin: left top;
		-ms-transform-origin: left top;
		transform-origin: left top;
		webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		-webkit-text-size-adjust: none;
		-ms-text-size-adjust: none;
		text-size-adjust: none;
		-webkit-touch-callout: none;
	}
	
	.news-list {
		height: auto;
		overflow: hidden;
		margin: 0 24px;
	}
	
	.news-item {
		padding: 18px 0 24px;
		border-bottom: 1px solid #EEE;
		height: auto;
		overflow: hidden;
	}
	
	.news-content {
		font-size: 16px;
		color: #363636;
		padding-bottom: 12px;
		margin-bottom: 0;
		line-height: 29px;
		vertical-align: baseline;
		overflow: hidden;
		display: -webkit-box;
	}
	
	.news-other {
		display: flex;
		-webkit-box-pack: justify;
		-webkit-justify-content: space-between;
		-ms-flex-pack: justify;
		justify-content: space-between;
	}
	
	span {
		color: #b6b6b6;
		font-size: 14px;
	}
	
	a {
		font-size: 14px;
		color: #0090F0;
		text-decoration: none;
	}
	
	.list-margin {
		padding: 46px 0;
		text-align: center;
		color: #767676;
		font-size: 16px;
		margin-bottom: 75px;
	}
</style>


