<template>
	<div class="select">
		<h5>请选择价格区间</h5>
		<div class="price">
			<span :class="{'sps':id==index}" v-for="(goods,index) in goods" @click="getSps(index)">{{goods}}</span>
		</div>
		<div class="push-button">
			<span v-for="(seal,index) in seals" v-text="seal.selt" @click="getSeal(index)" :class="{'active':bon==index}"></span>			
		</div>
	</div>
</template>
<script>
	import Vue from 'vue'
	import axios from 'axios'
	export default {
		name: 'onetour-filter',
		props:['goods'],
		data(){
			return {
				bon:0,
				id:null,
				seals:[
					{selt:'重置'},
					{selt:'确定'}
				]
			}
		},
		methods: {			
			getSeal(index){
				this.bon=index
			},
			getSps(index){
				this.id=index				
			}
		},
		mounted() {
			
		},
		watch: {　　
			all(curVal, oldVal) {
				this.getSlides(curVal)　　　　　　
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
	.select{
		background:#fff;
		z-index:110;
		margin-top:5rem;
		padding-left: 1.2rem;
		width:100%;
		h5{
			font-size: 0.8rem;
			font-weight: 500;
			color:#363636;
			padding:0.8rem 0;
		}
		.price{
			width:100%;
			span{
				float:left;
				width:50%;
				height:1.8rem;
				line-height: 1.8rem;
				font-size: 0.75rem;
				color:#363636;				
			}
			.sps{
				color:#0090f2;
			}
		}
		.push-button{
			position: fixed;
			bottom: 0;
			left:0;
			width:100%;
			height:2.2rem;
			line-height: 2.2rem;
			display: flex;
			flex-direction: row;
			justify-content: center;
			span{				
				width:50%;
				font-size: 0.7rem;
				color:#000;
				text-align: center;
			}
			.active{
				background:#0090f2 ;
				color:#fff;
			}
		}
	}
</style>


