<template>
	<div class="page-details">
		<div class="tff-container md-detail-duration">
			<ul class="md-detail-duration-tip tff-cards tff-no-border">
				<li class="ng-binding">
					<i class="iconfont icon-weixiao"></i> 4天行程
				</li>
				<li class="ng-binding">
					<i class="iconfont icon-dingwei"></i> 9个景点
				</li>
				<li class="ng-binding">
					<i class="iconfont icon-dingwei"></i> 1项自费
				</li>
				<li class="ng-binding">
					<i class="iconfont icon-dingwei"></i> 中文、英语
				</li>
			</ul>

			<div class="md-detail-fold-list" v-for="data,index in datas.data.itinerary">
				<div class="fold-item-head">
					<div class="fold-item-title">
						<span class="ng-binding">第{{index+1}}天</span>
						<i class="iconfont ng-binding" @click="choose(index)">∨</i>
					</div>
					<div class="fold-item-subtitle ng-binding" v-html="data.title">

					</div>
				</div>
				<ul class="fold-item-main" v-show="show && ids==index">
					<li class="fold-item-group">
						<div class="item-group-head">
							<i class="iconfont icon-dingwei"></i>概述
						</div>
						<div class="item-group-main ng-binding" v-html="data.summary"></div>
					</li>
					<li class="fold-item-group">
						<div class="item-group-head">
							<i class="iconfont"></i>酒店
						</div>
						<div class="item-group-main">
							<!-- ngRepeat: hotel in itinerary.hotel -->
							<div class="ng-binding" v-html="data.hotel[0]"></div>
							<!-- end ngRepeat: hotel in itinerary.hotel -->
							(或同等级酒店)
						</div>
					</li>
					<li class="fold-item-group">
						<div class="item-group-head">
							<i class="iconfont"></i>餐食
						</div>
						<div class="item-group-main ng-binding">早餐：自理<br><br>午餐：自理<br><br>晚餐：自理<br></div>
					</li>
					<li class="fold-item-group">
						<div class="item-group-head">
							<i class="iconfont"></i>景点
						</div>
						<div class="item-group-main attraction-list">
							<!-- ngRepeat: attraction in itinerary.attraction -->
							<div class="attraction-item ng-binding" v-for="li in data.attraction">
								<img :src="li.image.indexOf('http')==-1?'http://dn-toursforfun.qbox.me/images/'+li.image:li.image">{{li.name}}
							</div>

						</div>
					</li>
					<li class="fold-item-group ng-hide">
						<div class="item-group-head">
							<i class="iconfont"></i>自费项目
						</div>
						<div class="item-group-main">
							<ul>
								<!-- ngRepeat: ownexpense in itinerary.ownexpense -->
							</ul>
						</div>
					</li>
				</ul>
			</div>

		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import axios from 'axios'
	import { mapState } from 'vuex'
	var PageDetails = Vue.extend({
		name: "page-details",
		data() {
			return {
				show: false,
				datas: {},
				ids: ""
			}
		},
		props:["id"],
	
		methods: {
			choose(index) {
				this.ids = index
				this.show = !this.show
			},
			getSlides() {
				axios.get('https://app.toursforfun.com/api/product/101470116')
					.then(function(res) {
						this.datas = res.data
					}.bind(this))
			}
		},
		mounted() {
			this.getSlides()
		}
	})

	export default PageDetails
</script>

<style scoped lang="scss">
	.page-details {
		margin: 0;
		padding: 0;
		border: 0;
		vertical-align: baseline;
		font: inherit;
		font-size: 100%;
		.tff-container {
			margin: 0 24px;
			.md-detail-duration-tip {
				padding: 32px 0;
				display: flex;
				.ng-binding {
					flex: 1;
					text-align: center;
					font-size: 14px;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
					.iconfont {
						display: block;
						font-size: 24px;
						line-height: 24px;
						margin-bottom: 8px;
					}
				}
			}
			.md-detail-fold-list {
				border-top: 1px solid #EEE;
				.fold-item-head {
					padding-bottom: 32px;
					padding-top: 32px;
					.fold-item-title {
						display: flex;
						justify-content: space-between;
						font-size: 20px;
						font-weight: 700;
					}
					.fold-item-subtitle {
						font-size: 18px;
						margin-top: 16px;
					}
				}
				.fold-item-main {
					padding-top: 12px;
					.fold-item-group {
						margin-bottom: 32px;
						.item-group-head {
							font-size: 18px;
							margin-bottom: 24px;
							.iconfont {
								font-size: 24px;
								margin-right: 12px;
								vertical-align: middle;
							}
						}
						.item-group-main {
							color: #767676;
							font-size: 16px;
							.attraction-item {
								margin-bottom: 24px;
								text-align: center;
								img {
									width: 100%;
								}
							}
						}
					}
				}
			}
		}
	}
	
</style>


