<template>
	<div class="page-centent">
		<div class="banner swiper-container ">
			<ul class="banner-ul swiper-wrapper">
				<li class="banner-list swiper-slide">
					<img :src="datas.base.media.image_url" />
				</li>
				<li class="banner-list swiper-slide" v-for="imgs in datas.base.media.extra" >
					<img :src="imgs.url" />
				</li>
			</ul>

		</div>
		<div class="product-detail-subbar">
			<div class="product-detail-subbar-item" v-for="title,index in title" :class="{active:num==index}" @click="select(index)">
				{{title}}
			</div>
			
		</div>
		
		<page-shop v-show="num==0?true:false"></page-shop>
		<page-details  v-show="num==1?true:false"></page-details>
		
		<div class="product-detail-similar">
			<div class="tff-cards tff-no-border">
				<div class="tff-container">
					<div class="tff-cards-head">
						<i class="iconfont icon-xin"></i> 为您推荐
					</div>
				</div>
				<div class="similar-product-list tff-container">

					<div class="tff-product-item img-small" v-for="similar in similars.data">
						<div class="tff-product-item-img">
							<img class="show-img" :src="similar.image.indexOf('http')==-1?'http://dn-toursforfun.qbox.me/images/'+similar.image:similar.image" style="height: 90px;">
							<!-- 占位 -->
							<img src="../../../static/image/lazy-list.png" alt="" />
							<ul class="tff-product-item-tags">
								<li class="tag-img"><img src="http://m.toursforfun.com/img/detail/confirm-small.png"></li>

							</ul>
						</div>
						<div class="tff-product-item-info">
							<div class="product-name ng-binding" v-html="similar.product_name">

							</div>
							<div class="product-price">
								<span class="price ng-binding"><em class="txt-small ng-binding">¥</em>673.42</span><em class="txt-small">起</em>
							</div>
							<div class="txt-help txt-small ng-binding">
								已售 {{similar.number_of_order}}
							</div>

						</div>
					</div>
				</div>
			</div>
		</div>

		<div class="tff-scroll-done">
			<fieldset>
				<legend><i class="iconfont icon-weixiao"></i></legend>
			</fieldset>
		</div>

	</div>
</template>

<script>
	import Vue from 'vue'
	import axios from 'axios'
	import PageShop from './PageShop'
	import PageDetails from './PageDetails'
	import {mapState} from 'vuex'
	
	const PageCentent = Vue.extend({
		name: 'page-centent',
		
		data() {
			return {
				choose: true,
				datas: null,
				similars: {},
				title:["商品","详情","须知"],
				num:0,
			}
		},
		
		computed:{
				...mapState(['isShow',"id"])
		},
		methods: {
			gn(e) {
				alert()
			},
			getPosition(){
				this.$store.commit('getPosition')
			},
			select(index){
				this.num= index; 
			},
			getSlides() {

				var that = this

				function getUserAccount() {
					return axios.get('https://app.toursforfun.com/api/product/'+that.id);
				}

				function getUserPermissions() {
					return axios.get('https://app.toursforfun.com/api/product/'+that.id+'/similar');
				}
				axios.all([getUserAccount(), getUserPermissions()])
					.then(axios.spread(function(acct, similar) {
						//两个请求现已完成
						that.datas = acct.data.data
						that.similars = similar.data
					}));
			}
			
		},
		mounted(){
			let that=this
			this.$options.swiper=new Swiper(".swiper-container",{
					 pagination:'.swiper-pagination'
			})
			setTimeout(()=>{
				that.getSlides()
			},0)
		},
		components:{
			PageShop,
			PageDetails
		},
		watch:{
			
		}
		
		
	})



	export default PageCentent
</script>

<style scoped lang="scss">
	.page-centent {
		margin-bottom: 50px;
		.banner {
			height: 66vw;
			.banner-ul {
				height: 100%;
				.banner-list {
					list-style: none;
					img {
						width: 100%;
					}
				}
			}
		}
		.product-detail-subbar {
			height: 40px;
			padding: 0 24px;
			display: flex;
			justify-content: space-between;
			.product-detail-subbar-item {
				flex: 1;
				text-align: center;
				font-size: 16px;
				line-height: 40px;
				border-bottom: 2px solid #FFF;
				margin: 0 16px;
			}
			.active {
				color: #0090F3;
				border-bottom: 2px solid #0090F3;
			}
		}
		.product-detail-similar {
			background: #F5F5F5;
			padding-top: 8px;
			.tff-cards {
				padding: 32px 0;
				border-top: 1px solid #EEE;
				background: #FFFFFF;
				.tff-container {
					margin: 0 24px;
					.tff-cards-head {
						font-size: 18px;
						font-weight: 700;
						margin-bottom: 24px;
						i {
							font-size: 24px;
							vertical-align: middle;
						}
					}
				}
				.similar-product-list {
					margin: 0 24px;
					.tff-product-item {
						display: flex;
						justify-content: space-around;
						padding: 24px 0;
						border-top: 1px solid #EEE;
						.tff-product-item-img {
							width: 160px;
							min-height: 92px;
							background-image: url(../../../static/image/lazy-list.png);
							background-size: contain;
							background-repeat: no-repeat;
							position: relative;
							.show-img {
								margin: 0;
								width: 100%;
								max-height: 51vw;
								position: absolute;
								top: 0;
								left: 0;
								display: block;
							}
							img {
								margin: 0;
								width: 100%;
								max-width: 100%;
								max-height: 51vw;
							}
							.tff-product-item-tags {
								overflow: hidden;
								margin-top: 8px;
								.tag-img {
									float: left;
									margin-right: 4px;
									img {
										display: block;
										height: 16px;
										width: auto;
									}
								}
							}
						}
						.tff-product-item-info {
							padding-left: 16px;
							overflow: hidden;
							flex: 1;
							font-size: 14px;
							.product-name {
								overflow: hidden;
								text-overflow: ellipsis;
								display: -webkit-box;
								-webkit-line-clamp: 3;
								-webkit-box-orient: vertical;
								margin: 0 0 14px;
								font-size: 14px;
								line-height: 22px;
							}
							.product-price {
								font-size: 16px;
								margin-bottom: 8px;
								.price {
									color: #FF5400;
									font-weight: 700;
									.txt-small {
										font-size: .8em;
									}
								}
								em {
									font: inherit
								}
							}
							.txt-help {
								font-size: .8em;
								color: #B6B6B6
							}
						}
					}
				}
			}
		}
		.tff-scroll-done {
			background: #F5F5F5;
			padding: 12px 35%;
			fieldset {
				margin: 0;
				padding: 0;
				border: none;
				border-top: 1px solid #E4E4E4;
				legend {
					width: auto;
					border: none;
					font-size: 14px;
					line-height: normal;
					padding: 0 6px;
					margin: 0 auto;
					i {
						font-size: 24px;
						color: #E4E4E4;
					}
				}
			}
		}
	}
</style>


