<template>
	<div class="bourn-scroll bar">
		<div class="bourn_filterCity" v-if="mes">
			<div class="row">
				<div class="col">
					<h5>热门搜索</h5>
				</div>
			</div>
			<div class="row-hot-search">
				<div class="col">
					<span v-for="i in item">{{i.name}}</span> 	
				</div>
			</div>
		</div>
		<my-loading :isShow="isShow&&!mes"></my-loading>
		<div class="hot" v-if="!isShow&&!mes">
			<router-link v-for="(i,index) in item2" :to="{ name:'page',params:{id:i.new_product_id}}"><span @click="handleClick($event)">{{i.product_name}}</span><p>详情</p></router-link>
		</div>
		<router-link class="all" v-if="!isShow&&!mes" to="/list">查看全部{{item3}}个产品</router-link> 	
	</div>
</template>

<script>
	import axios from 'axios'
	import {mapGetters} from 'vuex'
	import Vue from 'vue'
	import MyLoading from '../loading/loading'
	Vue.use(MyLoading)
	export default {
		name: 'bourn-click',
		swiper: null,
		data() {
			return {
				item:'',
				item2:'',
				item3:'',
				mes:true,
				isShow:true,
				id:[]
			}
		},
		computed:mapGetters({
           msg:'GET_MSG'
       	}),
		methods: {
			getSlides() {
				axios.get('https://app.toursforfun.com/destination/combine/hot', {
					
				})
				.then(function(response) {
					this.item = response.data.data.hot
				}.bind(this))
			},
			getHot(num) {
				if(num==""){
					return
				}else{
					axios.get("https://app.toursforfun.com/search/entry?word="+num)
					.then(function(response) {
						this.item2 = response.data.data.search_data.product_list
						this.item3 = response.data.data.search_data.product_num
					}.bind(this))
				}
			},
			handleClick(e,a){
				console.log(e.target.innerText)
				console.log(this.$route)
			}
		},
		created(){
			this.mes=true
			this.getHot(this.msg)
       	},
		mounted() {
			this.getSlides()
		},
		updated() {
			
		},
		components: {
			MyLoading
		},
		watch: {
			msg(a,b){
				let that = this
				if(a ==""){
					this.mes=false
				}else{
					this.mes=false
					that.isShow = true
					setTimeout(() => {
						that.isShow = false
						this.getHot(a)
					}, 3000)
				}
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
	.bourn-scroll {
		flex-grow: 1;
		flex-shrink: 1;
		padding-top: 0.05rem;
		height: 100%;
		margin-bottom: 2.35rem;
		border-bottom: 1px solid rgba(88, 88, 88, .1);
		overflow: hidden;
		.bourn_filterCity {
			.row{
				display: flex;
				align-items: center;
				height: 2.8rem;
				padding: 0.5rem;
				color:#363636;
				font-size: 0.8rem;
			}
			.row-hot-search{
				.col{
					width: 100%;
					padding: 0.5rem;
					font-size:0.75rem;
				    overflow: hidden;
					span{
					    border-radius: 20px;
					    margin: 0.5rem 0.5rem 0 0;
					    float: left;
					    color: #0090F0;
					    padding: 0.25rem 0.5rem;
					    border: 1px solid #0090F0;
						box-sizing: border-box;					    
					}
				}
			}
		}
		.hot{
			padding: 0.5rem;
			width: 100%;
			a{
				display: flex;
				width: 100%;
				align-items: center;
				justify-content: space-between;
				padding: 0.8rem;
			    margin-top: 1px;
				font-size: 0.7rem;
				border-bottom: 1px solid #E0E0E0;
			}
			span{
				width: 71%;
				text-overflow:ellipsis;
				white-space: nowrap;
				overflow: hidden;
				line-height: 1rem;
			}
			p{
				color: #aaa;
			}
		}
		.all{
			text-align: center;
			display: block;
			color: #0090f2;
			padding: 1rem ;
			font-size: 0.8rem;
		}
	}
</style>


