<template>
  <div class="page-centent-box">
			<div class="tff-container">
				<div class="product-detail-base tff-cards tff-no-border">
					<div class="product-name">
						{{datas.base.name}}
					</div>

					<ul class="product-discount">
						<li class="product-discount-item">
							<img class="discount-icon" src="http://m.toursforfun.com/img/detail/discount.png">
							<div class="discount-txt">国家公园大环线来袭，低至75折起，快来参与吧~</div>
						</li>
					</ul>

					<!-- 销量和价格 -->
					<div class="product-price product-base-item">
						<span class="order-num">已售：{{datas.base.number_of_order}}</span>
						<div class="display-price">
							<span class="price">
									¥<b class="price-num">1827.27</b>
								</span>
							<i class="iconfont icon-yijian"></i>
						</div>
					</div>

					<!-- 产品标签 -->
					<ul class="product-tags product-base-item">

						<li class="tag-item" v-for="tag in datas.base.tags">{{tag.name}}</li>
					</ul>

					<div class="md-detail-city">
						<span class="product-city-name align-right">盐湖城出发</span>
						<i class="iconfont"></i>
						<span class="product-city-name">盐湖城结束</span>
					</div>
				</div>

				<div class="tff-cards product-detail-recommend-txt">
					<div class="tff-cards-head">
						<img class="txf-icon" src="http://m.toursforfun.com/img/detail/txf-logo.png">兔小风推荐
					</div>
					<div class="product-recommend-desc" :class="this.choose?'':'active'" v-html="datas.info.brief_description">
					</div>
					<div class="product-recommend-more">
						<a class="ng-binding" @click="gn()" v-text="this.choose?'查看更多':'收起'"></a>
					</div>
				</div>

				<div class="tff-cards md-detail-tips">
					<div class="tff-cards-head">
						<i class="iconfont">☆</i> 行程特色
					</div>
					<ul class="md-detail-tips-img">
						<li class="tips-img" v-for="tips in datas.info.tips">
							<img :src="tips">
						</li>
					</ul>
				</div>

				<div class="tff-cards product-detail-comment">
					<div class="tff-cards-head">
						<span class="ng-binding">
							<i class="iconfont icon-yijian"></i>
							评价（57条）
						</span>
						<span class="product-comment-percent">
							满意度：91.3%
						</span>
					</div>
					<div class="product-comment">
						<div class="product-comment-head">
							<span class="txt-gray">
								<p>1-6262****02</p>
								
								<div percent="80">
									<div class="tff-star-wrap">
										<div class="tff-star-inner" style="width:80%">
											
										</div>
									</div>
								</div>
							</span>
							<small class="txt-gray">
				            	<p>2017-06-26</p>
				            	
				            	<p>01:59:39</p></small>

						</div>
						<div class="product-comment-main">
							还不错，车程有点久，不过因为去了很多地方，酒店前两天的没有后几天的好，总体来说不错，美国旅游的话，吃真的是一个问题，没什么饭店
						</div>
					</div>
					<div class="product-comment-more">
						<a @click="getPosition">查看更多</a>
					</div>
				</div>
				<div class="tff-cards">
					<div class="product-detail-link-next">
						<a>点击查看详情 &nbsp;&gt;</a>
					</div>
				</div>

			</div>

		</div>

</template>

<script>
	import Vue from 'vue'
	import axios from 'axios'
	import {mapState} from 'vuex'
	const PageShop = Vue.extend({
		name: 'page-shop',
		 props:['showPosition'],
		data(){
			return{
				choose: true,
				datas:{}
			}
		},
		computed:{
			...mapState(['isShow',"id"])
	  },
		methods:{
			gn() {
				this.choose = !this.choose
			},
			getPosition(){
				this.$store.commit('getPosition')
			},
			getSlides() {
				var that=this
				axios.get('https://app.toursforfun.com/api/product/'+that.id)
				.then(function(res){
					this.datas=res.data.data
				}.bind(this))
			}
		},
		mounted(){
			this.getSlides()
		}
	})
	
	export default PageShop 
</script>

<style scoped lang="scss">
	.page-centent-box {
			width: 100%;
			margin-bottom: 50px;
			.tff-container {
				margin: 0 24px;
				.product-detail-base {
					padding: 32px 0;
					border-top: 1px solid #EEE;
					.product-name {
						font-size: 20px;
						line-height: 28px;
						font-weight: 700;
						margin-bottom: 24px;
						color: #363636;
					}
					.product-discount {
						margin-bottom: 24px;
						position: relative;
						.product-discount-item {
							font-size: 14px;
							margin-bottom: 12px;
							img {
								width: 22px;
								position: absolute;
								top: 0;
								left: 0;
							}
							.discount-txt {
								padding-left: 32px;
							}
						}
					}
					.product-price {
						display: flex;
						justify-content: space-between;
						margin-bottom: 24px;
						font-size: 14px;
						.order-num {
							color: #B6B6B6;
							margin-top: 4px;
							white-space: nowrap;
						}
						.display-price {
							padding-right: 28px;
							position: relative;
							text-align: right;
							color: #FF5400;
							.price-num {
								font-size: 24px;
							}
							.iconfont {
								position: absolute;
								right: 0;
								top: 0;
								width: 24px;
								height: 24px;
								font-size: 24px;
								line-height: 24px;
								vertical-align: middle;
								color: #FF6D00;
							}
						}
					}
					.product-tags {
						font-size: 14px;
						overflow: hidden;
						margin-bottom: 18px;
						.tag-item {
							float: left;
							line-height: 18px;
							color: #FF6D00;
							border: 1px solid #FF6D00;
							border-radius: 2px;
							margin-right: 4px;
							margin-bottom: 6px;
							padding: 0 6px;
						}
					}
					.md-detail-city {
						display: flex;
						justify-content: center;
						font-size: 16px;
						line-height: 24px;
						color: #767676;
						.product-city-name {
							flex: 1;
							white-space: nowrap;
							overflow: hidden;
							text-overflow: ellipsis;
						}
						i {
							margin: 0 12px;
							font-size: 24px;
							color: #363636;
						}
						.align-right {
							text-align: right;
						}
					}
				}
				.product-detail-recommend-txt {
					padding: 32px 0;
					border-top: 1px solid #EEE;
					.tff-cards-head {
						font-size: 18px;
						font-weight: 700;
						margin-bottom: 24px;
						img {
							height: 32px;
							width: 32px;
							margin-right: 12px;
							vertical-align: middle;
						}
					}
					.product-recommend-desc {
						color: #767676;
						font-size: 16px;
						height: 120px;
						line-height: 24px;
						overflow: hidden;
					}
					.active {
						height: auto
					}
					.product-recommend-more {
						font-size: 16px;
						color: #0090F3;
						text-align: right;
						margin-top: 16px;
						a {
							color: #0090F0;
						}
					}
				}
				.md-detail-tips {
					padding: 32px 0;
					border-top: 1px solid #EEE;
					.tff-cards-head {
						font-size: 18px;
						font-weight: 700;
						margin-bottom: 24px;
						i {
							font-size: 24px;
							vertical-align: middle;
						}
					}
					.md-detail-tips-img {
						.tips-img {
							img {
								width: 100%;
								display: block;
							}
						}
					}
				}
				.product-detail-comment {
					padding: 32px 0;
					border-top: 1px solid #EEE;
					.tff-cards-head {
						font-size: 18px;
						font-weight: 700;
						margin-bottom: 24px;
						display: flex;
						justify-content: space-between;
					}
					.product-comment {
						.product-comment-head {
							display: flex;
							justify-content: space-between;
							align-content: space-between;
							margin-bottom: 24px;
							color: #B6B6B6;
							font-size: 14px;
							.txt-gray {
								color: #767676;
								.tff-star-wrap {
									display: inline-block;
									width: 70px;
									height: 17px;
									background: url(../../../static/image/star-off.png) 0 3px repeat-x;
									background-size: 14px 14px;
									padding-top: 3px;
									.tff-star-inner {
										height: 100%;
										background: url(../../../static/image/star_on.png) repeat-x;
										background-size: 14px 14px;
										width: 80%;
									}
								}
							}
							small {
								font-size: 85%;
								p {
									padding-top: 3px
								}
							}
						}
						.product-comment-main {
							color: #767676;
							line-height: 21px;
							font-size: 16px;
						}
					}
					.product-comment-more {
						font-size: 16px;
						color: #0090F3;
						text-align: right;
						margin-top: 16px;
						a {
							color: #0090F0;
							text-decoration: none;
						}
					}
				}
				.tff-cards {
					padding: 32px 0;
					border-top: 1px solid #EEE;
					.product-detail-link-next {
						font-size: 16px;
						text-align: center;
						a {
							color: #0090F0;
							text-decoration: none;
						}
					}
				}
			}
		}
</style>



