<template>
	<div class="login-form main">
		<section class="phone-login">
			<div class="form-item phone-code" >
				<span v-for="i in cit">{{i}}</span>
				<i class="iconfont icon-login-code" @click="country"></i>
			</div>
			<div class="form-item ">
				<i class=" iconfont icon-login-user"></i>
				<input type="tel" placeholder="输入手机号" v-model="newname">
			</div>
			<div class="form-item ">
				<i class=" iconfont icon-login-varify"></i>
				<input type="text" placeholder="短信验证码" class="varify_input">
				<div class="varify_btn">
					<button class="btn no-wrap btn-default" @click="huoqu">获取验证码</button>
				</div>
			</div>
			<div class="form-item has-icon">
				<i class="form-item-icon iconfont icon-sign-pwd"></i> 
				<input type="text" placeholder="5-20位密码，包含字母和数字" v-show="mes"  v-model="newpassword">
				<input type="password" placeholder="5-20位密码，包含字母和数字" v-show="!mes"  v-model="newpassword">
				<i class="form-item-icon iconfont icon-sign-clear" v-show="mes"></i> 
				<i class="form-item-icon iconfont icon-sign-show" v-show="!mes"></i>
			</div>
			<div class="login-submit-btn" v-show="!coun">
				<button class="btn btn-lg btn-default" @click="register">注册</button>
			</div>
			<div class="pre-panel" v-show="coun">
				<div class="panel-head">
					<span>请选择国际区号</span> 
					<span class="right-cancel" @click="country">取消</span>
				</div>
				<div class="panel-main">
					<div class="list-item" v-for="(i,index) in item" :class="{'active':act===index}" @click="choose(index,$event)" >{{i.city}}&nbsp;----{{i.qu}}</div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'login-a',
		data() {
			return {
				coun: false,
				mes:false,
				act:"",
				newname:'',
				newpassword:'',
				yzm:'',
				cit:['中国','+86'],
				item: [
					{"city": "中国","qu": "+86"},
					{"city": "美国","qu": "+1"},
					{"city": "台湾","qu": "+886"},
					{"city": "加拿大","qu": "+1"},
					{"city": "香港","qu": "+852"},
					{"city": "澳大利亚","qu": "+61"},
					{"city": "英国","qu": "+44"},
					{"city": "新加坡","qu": "+65"},
					{"city": "日本","qu": "+81"},
					{"city": "法国","qu": "+33"}
				]
			}
		},
		methods: {
			country() {
				this.coun = !this.coun
			},
			choose(index,e){
				this.cit = e.target.innerText.split("----")			
				this.coun = !this.coun
				this.act = index
			},
			register(){
			    if(this.newname == "" || this.newpassword == ""){
			        
			    }
			    else{
			       	axios.get("http://datainfo.duapp.com/shopdata/userinfo.php")
			        .then((res)=>{
			            console.log(res)
			        })
			    }
		    	
			},
			huoqu(){
			}
		},
		mounted() {
			
		},
		updated() {
			
		},
		watch: {
			
		}
	}
</script>
<style scoped lang='scss'>
	.login-form {
		display: flex;
		justify-content: center;
		overflow: auto;
		height: 100%;
		.account-login,.phone-login {
			width: 90%;
		}
		.form-item {
			padding: 10px 0;
			border-bottom: 1px solid #d6d6d6;
			display: flex;
			align-items: center;
			height: 3.25rem;
			input {
				flex: 1;
				height: 2.2rem;
				padding: 0.2rem 0.3rem;
				font-size: 0.7rem;
				border: none;
			}
			span:nth-child(1){
			font-size: 0.75rem;
			}
			span:nth-child(2) {
				margin:0 0.6rem;
				font-size: 0.7rem;
				color: #0090f2;
			}
		}
		.btn {
			width: 100%;
			background: #d6d6d6;
			border: none;
			color: #fff;
			border-radius: 5px;
			height:2.2rem;
			padding:0 10px;
		}
		.login-submit-btn {
			margin: 1rem 0;
			.btn {
				font-size: 1rem;
				height: 2.5rem;
			}
		}
		.login-other-option {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-top: 0.3rem;
			font-size: 0.6rem;
		}
		.icon-login-code {
			width: 0;
		    height: 0;
		    border-bottom: 0.5rem solid #0090f2;;
		    border-left: 0.5rem solid transparent;
		}
	}
	.pre-panel{
		font-size:0.8rem;
		width: 100%;
		height: 100%;
		.panel-head{
			justify-content: space-between;
		}		
		.panel-head,.list-item{
			display: flex;
			align-items: center;
			box-shadow: 0 1px 2px rgba(88, 88, 88, 0.1);
			line-height: 2.75rem;
		}
		.active{
			color:#0090f2 ;
		}
	}
</style>


