<template>
	<header>
		<transition name='fade'>
			<div class="bar bar-header" v-show="mes">
				<h1 class="title">目的地</h1>
			</div>
		</transition>
			<div class="bar bar-input">
				<label class="">
					<i class="icon iconfont icon-search"></i>
					<input type="search"  placeholder="搜索目的城市"  @click="change" v-model="val" @change="inputMsg">
				</label>
				<span v-show="ms"  @click="changebtn">取消</span>
			</div>
	</header>
</template>

<script>
	import axios from 'axios'
	import {mapState} from 'vuex'
	export default {
		name: 'bourn-header',
		data() {
			return {
				mes:true,
				ms:false,
				val:''
			}
		},
		methods:{
			change(){
				this.mes=false
				this.ms=true
				this.$emit('child-say',this.mes);
				this.saveName=''
			},
			changebtn(){
				this.mes=true
				this.ms=false
				this.$emit('child-say',this.mes);
			},
			inputMsg(e){
				this.val=''
		      	this.$store.commit('SET_MSG',e.target.value)
	   		},
		}
	}
</script>

<style scoped lang='scss'>
	.fade-enter-active {
	  	transition: all 2s;
	}
	.fade-leave-active {
		transform: translateY(-100px);
	  	transition: all 0s;
	}
	.fade-enter, .fade-leave-active {
	  	transform: translateY(-100px);
	}
	header {
		flex-grow: 0;
		flex-shrink: 0;
		.bar {
			box-shadow: 0 1px 2px rgba(88, 88, 88, .1);
		}
		.bar-header {
			color: #363636;
			border-color: #EEE;
			background-color: #FFF;
			text-align: center;
			height: 2.15rem;
			box-shadow: 0 1px 2px rgba(88, 88, 88, .1);
			h1 {
				line-height: 2.15rem;
				font-size: 0.8rem;
				color: #363636;
			}
		}
		.bar-input {
			padding: 0.42rem;
			display: flex;
			justify-content: space-around;
			align-items: center;
			label {
				width: 100%;
				flex-grow: 1;
				flex-shrink:1;
				background: #eee;
				border-radius: 20px;
				padding: 0 0.4rem;
				display: flex;
				i {
					vertical-align: middle;
				    display: flex;
		    		align-items: center;
		    		font-size: 0.5rem;
				}
				input {
					width: 90%;
					padding-left: 8px;
					height: 28px;
					background: none;
					border: none;
					outline: none;
					color: #111;
				}
			}
			span{
				flex-grow: 0;
				flex-shrink: 0;
				width: 15%;
				font-size: 0.8rem;
				text-align: center;
			    color: #b2b2b2;
			}
		}
	}

</style>


