<template>
	<header id="header" class="header-box">
		<h1 v-if="isShow">
			<span @click="getChange" class="iconfont icon-search"></span>
			<span>首页</span>
			<span @click="changePhone"  class="iconfont icon-dianhua"></span>
		</h1>
		<div class="header-box">
			<div class="swiper-container">
		        <div class="swiper-wrapper">
		            <div class="swiper-slide" v-for="(banner,index) in banners" :ket="banner">
		            	<a class="box" href="http://cn.toursforfun.com/experiencer-201702?ici=m-banner&amp;icn=ys2017">
							<img :src="'http://dn-toursforfun.qbox.me/images/'+banner.picture">
						</a>
					</div>
		        </div>
		        <!-- Add Pagination -->
		        <div class="swiper-pagination"></div>
		    </div>
			<div v-if="!isShow" class="gjc">
				<span class="iconfont icon-search"></span>
				<span @click="getChange" class="souproduct">搜索关键词、产品ID</span>
				<span @click="changePhone"  class="iconfont icon-dianhua"></span>
			</div>
		</div>
		 
	</header>
</template>

<script>
	import axios from  'axios'
	import {mapState} from 'vuex' 
export default {
  name: 'home-header',
  props:['getChange'],
  swiper:null,
  data () {
    return {
    	isShow:false,    	
     	banners:[] ,
     	scroll:''
     	
    }
  },
  computed:{
  	...mapState(['phone'])
  },
  methods:{
  	getBanner(){
  		axios.get('https://app.toursforfun.com/config/index/v1')
  		.then(function(response){ 			
  			this.banners = response.data.data.banner  			
  		}.bind(this))  		
  	},
  	getScroll(){  
  		let that = this
  		this.scroll = document.body.scrollTop;
  		if(this.scroll>=200){ 			
  			that.isShow=true
  		}else{
  			that.isShow=false
  		}  	},
  	changePhone(){
		this.$store.commit('changePhone')
  	}
 	
  },
  mounted(){
  	this.getBanner()
  	this.$options.swiper = new Swiper('.swiper-container', {
        pagination: '.swiper-pagination',
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
        paginationClickable: true,
        spaceBetween: 30,
        centeredSlides: true,
        autoplay: 2500,
        autoplayDisableOnInteraction: false
   }) 
  
   window.addEventListener('scroll', this.getScroll)
  },
  updated(){
	
  },
  watch:{
  	banners(val){  		
  		let that = this
  		setTimeout(()=>{  		  			
  			that.$options.swiper.update()
  		},0) 	 		
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
#header{
	position: relative;
	background:#fff;
	height: 8.95rem;	
	text-align: center;	
	width: 100%;	
	h1{
		width:100%;		
		height:2.15rem;
		line-height:2.15rem;
		background:#fff;
		position: fixed;
		font-size:0.8rem;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		z-index:110;
		.iconfont{
			font-size:1.1rem;	
		}
	}
	.swiper-pagination-bullet-active{
		background:#fff;
	}
	.swiper-slide{
		width:100%;
		height:8.95rem;
		.box{
			img{
				max-width: 100%;
				min-height:100%;
			}
		}		
	}
	.gjc{
		position: relative;
		margin: 0 auto;
		z-index: 1000;
    	transform: translateY(-11px);
		width:13.6rem;
		height:2.2rem;	
		line-height: 2.2rem;	
		border-radius: 1.5rem;
		box-shadow: 0 0.25rem 0.3rem 0 #dedede;
		background: #fff;
		z-index:10;
		span{
			display: inline-block;
		}
		.iconfont{
			font-size: 1rem;
		}
		.icon-search{
			color:#0090f2;
		}
		.icon-dianhua{
			color:#a6a6a6;
		}
		.souproduct{			
		    padding-right: 1.6rem;	
		      
		    font-size: 0.8rem;
		    border-right: 1px solid #e0e0e0;
		    color: #0090f2;
		    line-height:1.2rem;
		}
	}
}
</style>



