		data() {
			return {
			}
		},
		methods: {
		},
		components: {
			MineHeader,
			MineIcon,
			MineList
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.mine{
		line-height: 1;
		background: #f3f3f7;
		height: 100%;
	}
</style>


