<template>
	<div class="bourn root-box">
		<bourn-header v-on:child-say="Son"></bourn-header>
		<transition name='fade'>
			<bourn-main v-show="mes"></bourn-main>
		</transition>
		<transition name='fade'>
			<bourn-click v-show="ms"></bourn-click>
		</transition>
		<nav-tab v-show="mes"></nav-tab>
	</div>
</template>

<script>
	import NavTab from './NavTab'
	import BournHeader from './BournComponents/BournHeader'
	import BournMain from './BournComponents/BournMain'
	import BournClick from './BournComponents/BournClick'
	export default {
		name: 'bourn',
		data() {
			return {
				mes:true,
				ms:false
			}
		},
		methods: {
		 	Son(data){
              	this.mes = data
              	this.ms = !data
            }
		},
		components: {
			BournHeader,
			BournMain,
			BournClick
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.fade-enter-active {
	  	transition: all 2s;
	}
	.fade-leave-active {
	  	transition: all 0s;
	}
	.fade-enter, .fade-leave-active {
	  	transform: translateY(1000px);
	}
	.bourn {
		display: flex;
		flex-flow: column;
		justify-content: space-between;
		height: 100%;
	}
</style>


