<template>
	<div class="bourn-scroll bar">
		<div class="bourn_filterCity">
			<ul class="bourn_fcTags scroll">
				<li class="bourn_fctItem" v-for="(item,index) in items" @click="change(index)" :class="{'active':all==index}">{{item.select}}</li>
			</ul>
			<ul class="bourn_fcCountries scroll">
				<li class="bourn_fctItem" v-for="(item,index) in item" @click="changeCity(index)" :class="{'active':id==index}">{{item.name}}</li>
			</ul>
			<ul class="bourn_fcCities scroll" v-if="mes">
				<li class="bourn_fctItem" v-for="(item,index) in item[id==0 ? 1 : id ].city">{{item.name}}</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import axios from 'axios'
	export default {
		name: 'bourn-main',
		swiper: null,
		data() {
			return {
				item: '',
				all: 0,
				id: 0,
				mes: false,
				active: false,
　　　　　　　　items: [
					{select: '热门'}, 　　　　　　　　　　 
					{select: '美洲'}, 　　　　　　　　　　 
					{select: '澳新'}, 　　　　　　　　　　 
					{select: '欧洲'}　　　　　　　　
				]
			}
		},
		methods: {
			getSlides(num) {
				axios.get('https://app.toursforfun.com/destination/combine/hot')
					.then(function(response) {
						switch(num) {
							case 0:
								this.item = response.data.data.hot;
								break;
							case 1:
								this.item = response.data.data.am;
								break;
							case 2:
								this.item = response.data.data.au;
								break;
							case 3:
								this.item = response.data.data.eur;
								break;
							default:
								break;
						}
					}.bind(this))
			},
			change(index) {
				if(index==0){
					this.mes=false
				}else{
					this.mes=true
				}
				this.all=index
				this.id = 0
			},
			changeCity(index) {
				this.id = index　　　　
			}
		},
		mounted() {
			this.getSlides(this.all)
			this.change(0)
		},
		watch: {　　
			all(curVal, oldVal) {
				this.getSlides(curVal)　　　　　　
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
	.bourn-scroll {
		flex-grow: 1;
		flex-shrink: 1;
		padding-top: 0.05rem;
		height: 100%;
		margin-bottom: 2.35rem;
		border-bottom: 1px solid rgba(88, 88, 88, .1);
		overflow: hidden;
		.bourn_fctItem {
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
			width: 100%;
			height: 100%;
		}
		.bourn_filterCity {
			display: flex;
			width: 100%;
			font-size: 0.8rem;
			cursor: pointer;
			height: 100%;
			.bourn_fcTags {
				width: 17%;
				background-color: #f4f3f8;
				border-right: 1px solid #E0E0E6;
				.bourn_fctItem {
					height: 3.2rem;
					text-align: center;
					line-height: 3.2rem;
				}
				.active {
					width: 102%;
				}
			}
			.bourn_fcCountries,.bourn_fcCities {
				padding-left: 0.5rem;
				overflow-y: scroll;
				.bourn_fctItem {
					cursor: pointer;
					height: 54px;
					line-height: 54px;
					border-bottom: 1px solid #E0E0E6;
					transform: translate3d(0, 0, 1);
				}
			}
			.bourn_fcCountries {
				border-right: 1px solid #E0E0E6;
				width: 27%;
			}
			ul:nth-last-child(1) {
				flex-grow: 1;
				flex-shrink: 1;
			}
		}
		.active {
			color: #0092F2;
			background: #fff;
		}
	}
</style>


