<template>
  <div>
    <header>
    	<div class="header_a">
    		<router-link to="/Home">
    			<span  class="iconfont icon-xiaoyuhao"></span>
    		</router-link>    		
    		<span class="a_sp">一日游</span>
    		<span  class="iconfont icon-weixiao"></span>
    	</div>
    	<div class="header_b">
    		
    		<span v-for="(name,index) in names" @click="getName(index)" :class="{'active':title==index}">{{name.cname}}</span>    		
    	</div>
    </header>
   <my-loading :isShow="isShow"></my-loading>   
   	<div class="product-box" v-if="tabs">
			<div class="product" v-for="destination in destinations">
				<img :src="destination.image" alt="" />
				<div class="Hinfomation">
					<div class="Hname">
						<h4>
							{{destination.name}}
						</h4>
					</div>
					<div class="Hdata">
						<div class="num">
							<span>已售</span>
							<span>{{destination.order_count}}</span>
						</div>
						<div class="price">
							<span class="price-sp1">￥</span>
							<span class="price-sp2">{{destination.default_price*6.7 |formatMoney}}</span>
							<span class="price-sp3">起</span>
						</div>
					</div>	
				</div>			
			</div>
		</div>
		<!--目的地-->
				
		<div class="bourn">
			<div v-show="title==0">
				<onetour-mdd></onetour-mdd>
			</div>
			<div v-show="title==1">
				<onetour-filter :goods="goods"></onetour-filter>
			</div>
			<div v-show="title==2">
				<onetour-recommend :product="product"></onetour-recommend>
			</div>
		</div>		
  </div>
</template>

<script>
	import axios from  'axios'
	import {mapState} from 'vuex'
	
	import OnetourMdd from './OnetourMdd'
	import OnetourFilter from './OnetourFilter'
	import OnetourRecommend from './OnetourRecommend'
	
	import MyLoading from '../loading/loading'
	import Vue from 'vue'	
	Vue.use(MyLoading)
	
	
	
	
export default {
  name: 'homeonetour',
  props:['getTab'],
  data () {
    return {
     	destinations:[],
     	grooms:[],
     	goods:{},
     	product:{},
     	isShow:true,
     	title:null,
     	tabs:true,
     	names:[
     		{cname:'目的地'},
     		{cname:'筛选'},
     		{cname:'推荐产品'}
     	]
    }
  },
  computed:{
  	...mapState(['tab'])
  },
  filters:{
		formatMoney:function (value) {
		    return value.toFixed(2)
		}
  },
  methods:{
  	gettOnetour(){
  		axios.get('https://app.toursforfun.com/api/search/category?page=1&pageSize=10&product_line=activity')
  		.then(function(response){
  			this.goods = response.data.data.conditions.priceRange
  			this.grooms = response.data.data;
  			this.destinations = response.data.data.product_list ;
  			this.product =response.data.data.conditions.product_tags
  			console.log(this.goods)
  		}.bind(this))
  	},
  	getName(index){  
  		this.title=index
  		this.tabs =false

  	},
  	changeTab(){
			this.$store.commit('changeTab')
  	}
  },
  mounted(){
  	
  	let that = this  	
  	setTimeout(()=>{
  		that.gettOnetour()
  		that.isShow=false
  	},500)
  	
  },
  components:{
  	MyLoading,
  	OnetourMdd,
  	OnetourFilter,
  	OnetourRecommend
  },
  witch:{
  	title(a,b){
  		console.log(a)
  		console.log(b)
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	header{
		position: fixed;
		top:0;
		left:0;
		width:100%;
		background: #fff;
		z-index:10;
		.header_a{
			width:100%;
			height:2.15rem;
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			align-items: center;
			color:#000;
			font-size: 1.3rem;
			.iconfont{
				font-size: 1.3rem;
			}
			.a_sp{
				font-weight: bold;
				font-size: 1rem;
			}
		}
		.header_b{
			width:100%;
			height:2.2rem;
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			align-items: center;
			font-size: 0.8rem;
			box-shadow: 0 1px 2px rgba(88,88,88,.1);
		}		
	}
	.product-box{
		margin-top:5rem;
		padding: 0 1.2rem;
    background-color: #fff;
		.product{
			img{
				width:100%;
				height:7.85rem;
			}
		}
		.Hinfomation{
			padding: 0.8rem 0;
    		border-bottom: 1px solid #e0e0e0;
			.Hname{
				h4{					
					line-height:1rem;					
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					font-weight: 400;					
					margin:0.5rem 0;
					font-size:0.7rem ;
					color:#363636;
				}			
			}
			.Hdata{
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				.num{
					span{
						color:#b6b6b6;
						font-size: 0.6rem;
					}
				}
				.price{
					.price-sp1{
						font-size: 0.4rem;
						color:#fb5f10;
					}
					.price-sp2{
						font-size: 1.2rem;
						color:#fb5f10;
						font-weight: 400;
					}
					.price-sp3{
						font-size: 0.4rem;
					}
				}
			}	
		}			
	}
	.bourn {
		display: flex;
		flex-flow: column;
		justify-content: space-between;
		height: 100%;
	}
	.active{
		font-weight: 600;
		color:#0090f2;
	}
</style>
<!--product.image.indexOf('http')!=-1?product.image:'http://dn-toursforfun.qbox.me/images/'+product.image--> 


