<script>


	

import axios from 'axios'


export default {
  name: 'activity',
  data () {
    return {
     messageList:[],
     imgsrc:null,
     imgsrc2:null
    }
  },
  methods:{
  	getMessage(){
  		axios.get('../static/active.json')
			.then(function(response){
				this.messageList = response.data
				this.imgsrc = response.data.image1
				this.imgsrc2 = response.data.image2
				console.log(this.imgsrc )
			}.bind(this))

  	},
  	swiper(){
		  	this.swiper=new Swiper(".swiper-container",{
				      pagination: '.swiper-pagination',
			        paginationClickable: '.swiper-pagination',
			        nextButton: '.swiper-button-next',
			        prevButton: '.swiper-button-prev',
			        observer:true,
		          observeParents:true
				})
		}
  },
  mounted(){
  	let that=this
  	setTimeout(()=>{
  			that.getMessage()
  			that.swiper()
  	},2000)
  }
}
</script>

