<template>
	<div id="login">
		<div class="login-bar">
			<i class="iconfont icon-xiaoyuhao" @click="routerBack"></i>
			<p>登录</p>
			<i class="iconfont icon-xiaoyuhao icon"></i>
		</div>
		<div class="login-tab">
			<div class="tab-item" v-for="(i,index) in item"  :class="{'active':mes===index}" @click="activeClick(index)">{{i}}</div>
		</div>
		<login-a></login-a>
	</div>
</template>

<script>
	import NavTab from './NavTab'
	import LoginA from './LoginComponents/LoginA'
	export default {
		name: 'login',
		data() {
			return {
				item:["账号登录","手机验证码登录"],
				mes:0,
			}
		},
		methods: {
			routerBack() {
				this.$router.go(-1);
			},
			activeClick(index){
				this.mes=index
			}
		},
		mounted() {
		},
		components: {
			LoginA
		},
		watch:{
			mes(a,b){
				if(a==true){
					this.$children[0].mes=false
				}else{
					this.$children[0].mes=true
				}
			}
		}
	}
</script>

<style scoped lang="scss">
	#login{
		height: 100%;
		overflow: hidden;
	}
	.login-bar {
		width: 100%;
		height: 2.4rem;
		line-height: 0.9rem;
		display: flex;
		align-items: center;
		box-shadow: 0 1px 2px rgba(88, 88, 88, .1);
		p {
			flex: 1;
			text-align: center;
		}
		.icon-xiaoyuhao{
			font-size: 1.3rem;
		}
		.icon{
			color: #fff;
		}
	}
	.login-tab{
		display: flex;
		height: 3rem;
		font-size:0.8rem ;
		padding: 0 1rem;
		.tab-item {
			text-align: center;
			width: 50%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.active{
		    color: #0090f2;
			border-bottom: 3px solid #0090f2;
		}
		
	}
</style>


