import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import Bourn from '@/components/Bourn'
import Aide from '@/components/Aide'
import Message from '@/components/Message'
import Mine from '@/components/Mine'
import Login from '@/components/Login'
import Sign from '@/components/Sign'
import Page from '@/components/pages/page'
import details from '@/components/details'
import item from '@/components/Item'
import activity from '@/components/Activity'
import HomeOnetour from '@/components/HomeComponents/HomeOnetour'
import List from '@/components/List'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',redirect:"/home"},
    {path: '*',redirect:"/home"},
    {path: '/home',name:'home',component:Home},
    {path: '/bourn',name:'bourn',component:Bourn},
    {path: '/aide',name:'order',component:Aide},
    {path: '/message',name:'message',component:Message},
    {path: '/mine',name:'mine',component:Mine},
    {path: '/login',name:'login',component:Login},
    {path: '/sign',name:'sign',component:Sign},
    {path: '/page/:id',name:'page',component:Page},
 		{path: '/details/:dataId',name: 'details',component: details},
    {path: '/item/:dataId',name: 'item',component: item},
    {path: '/activity/:dataId',name: 'activity',component: activity},
 		{path: '/homeonetour',name:'homeonetour',component:HomeOnetour},
 		{path: '/list',name:'list',component:List}
  ]
})



