<template>
  <div class="item root-box">
  	<a class="back" href="/">{{head.name}}</a>
  	  <img :src="src"/>
  	  <div class="product-box" v-for="list in messageList">
  	  	<div  class="product-item-block">
            <router-link class="product-item" :to="{ name:'page', params: { id: list.new_product_id,dataId:list.product_id}}" >
            	<div class="product-img">
								<img :src="list.image.indexOf('http')!=-1?list.image:'http://dn-toursforfun.qbox.me/images/'+list.image+'?imageView2/1/w/420/h/243/q/75/format/jpg'" />
              </div>
							<div class="product-info">
								<h4 class="title ng-binding">
									{{list.product_name}}
								</h4>

								<div class="price-wrap">
									<div class="list-tag" >
										<i class="tag confirm" v-if="list.is_free_sale" ></i>
										<i class="tag group" v-if="list.is_promotion"></i>
										<i class="tag special" v-if="list.is_special"></i>
									</div>
									<div class="count">
										已售 <span class="value ng-binding">{{list.order_count}}</span>
									</div>
									<div class="price">
										<span class="ng-binding">¥</span>
										<span class="value ng-binding">{{list.default_price*6.7-list.special_price | formatMoney}}</span>
										<span class="price-qi">起</span>
									</div>
								</div>
							</div>
						</router-link>
            <i class="discount-tag" v-if="(1-list.special_price/(list.default_price*6.7))*10>=10?0:1">{{(1-list.special_price/(list.default_price*6.7))*10 | storeMoney}}<span>折</span></i>
          </div>
  	  </div>
	</div>
  	
</template>

<script>


	

import axios from 'axios'


export default {
  name: 'item',
  data () {
    return {
     messageList:[],
     src:null,
     head:''
    }
  },
  filters:{
		formatMoney:function (value) {
		    return value.toFixed(2)
		},
		storeMoney:function (value) {
		    return value.toFixed(1)
		}
  },	
  methods:{
  	getMessage(){
  		axios.get('https://app.toursforfun.com/config/banner/'+this.$route.params.id1+'/products')
			.then(function(response){
				this.src="http://dn-toursforfun.qbox.me/images/"+response.data.data.picture+"?imageView2/1/w/720/h/404/q/90/format/jpg"
				this.messageList=response.data.data.products_info
				this.head=response.data.data
			}.bind(this))

  	}
  },
  mounted(){
  	let that=this
  	setTimeout(()=>{
  			that.getMessage()
  	},2000)
  }
}
</script>

<!-- Add 'scoped" attribute to limit CSS to this component only -->
<style scoped>
	.root-box{
		background: rgb(1, 203, 189);
	}
.back{
	display: block;
	width: 100%;
	text-align: center;
	height: 44px;
	line-height: 44px;
}
img{
	width: 100%;
}
a{
	  color: #0090F0;
    text-decoration: none;
    background: #FFFFFF;
}
.product-box{
	background: rgb(1, 203, 189);
	padding: 24px;
}
.product-item-block {
    position: relative;
}
.product-item {
    padding: 0;
    margin-bottom: 24px;
    background-color: #fff;
    border: none;
    height: auto;
    overflow: hidden;
    display: block;
}
.product-img {
    width: 100%;
    min-height: 49vw;
    margin-right: 16px;
    background-image: url("http://m.toursforfun.com/img/lazy.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.product-img img {
    margin: 0;
    border: 0;
    width: 100%;
    max-height: 51vw;
}
.product-info {
    display: block;
    color: #363636;
}
h4.title {
    line-height: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin: 24px 12px;
    font-size: 14px;
    color: #363636;
}

.price-wrap {
    margin: 0 12px 24px;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: baseline;
    -webkit-align-items: baseline;
    -ms-flex-align: baseline;
    align-items: baseline;
    padding-top: 3px;
    font: 12px Helvetica Neue Regular;
    color: #363636;
}
.list-tag {
    height: 16px;
    font-size: 0;
    margin-top: -1px;
}
.count {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    margin-top: -2px;
    height: 18px;
    color: #b6b6b6;
    line-height: 18px;
}
.price {
    font-size: 12px;
    line-height: 8px;
    text-align: right;
    color: #fb5f10;
}
.tag {
    width: 16px;
    height: 16px;
    background-size: 100%;
    background-repeat: no-repeat;
    display: inline-block;
}
.tag.confirm {
    width: 60px;
    background-image: url("http://m.toursforfun.com/img/detail/confirm-small.png");
}
.tag.group {
    background-image: url("http://m.toursforfun.com/img/detail/group-small.png");
}
.tag.special {
    background-image: url("http://m.toursforfun.com/img/detail/special-small.png");
}
i span {
    margin-right: 12px;
    font-size: 12px;
}
.price span {
    font-family: "Helvetica Neue","Microsoft Yahei","-apple-system",Helvetica,STHeiTi,sans-serif;
}
.price .value {
    font-size: 24px;
    font-weight: 700;
}
.price .price-qi {
    color: #363636;
}
.discount-tag {
    width: 48px;
    height: 48px;
    position: absolute;
    display: block;
    top: 12px;
    left: 12px;
    background-color: #363636;
    border-radius: 50%;
    color: #FFF;
    line-height: 48px;
    font-weight: 600;
    font-size: 18px;
    text-align: center;
}
</style>



